.ui.cards {
    .ui.fluid.card {
      .ui.segment {
        position: fixed;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        }
    }
  }


  .ui.mini.statistic {
    align-items: flex-start;
}

img.ui.image {
  height: 150px;
}

.ui.card {
  .header {
    .value {
      height: 60px;
    }
  }
}

.ui .button:hover {
  cursor: pointer	;
}

.ui.menu.fluid, .ui.vertical.menu.fluid {
  align-items: center;
  justify-content: center;
}

.ui.segment {
  height:  71px;
}

.ui.list {
  .ui.centered.cards {
    .ui.fluid.card {
      .header {
        display: flex;
        justify-content: center;
      }
    }
  }
}

#title {
  height: auto;
}